const mapDispatchToProps = (dispatch, { theGuardianService }) => {
  return bindActionCreators(
    {
      fetchPosts: fetchPosts(theGuardianService)
    },
    dispatch
  );
};

export default withTheGuardianService()(
  connect(
    null,
    mapDispatchToProps
  )(Header)
);
